@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{margin:0}.start{height:100vh;width:100vw;background-color:#F4F7FA;display:flex;flex-direction:column;justify-content:center;align-items:center}.start__box{padding:1rem;display:flex;position:relative;flex-direction:column;justify-content:center;align-items:center;background-color:white;border-radius:2px;box-shadow:0 2px 6px 0 rgba(0,0,0,0.2);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.start__button,.start__button--alt{padding:0.5rem 2rem;border:0.5px solid #523EE8;color:#523EE8;border-radius:5px}.start__button--alt{color:white;background-color:#523EE8}.start__instruct{padding:1rem;border-radius:5px;height:60%;width:25%;border:0.5px solid #523EE8;display:flex;flex-direction:column;align-items:center;margin-right:2rem}

.repo__card,.repo__card--alt{margin:1em 0;max-width:100%;position:relative;display:flex;justify-content:center;width:400px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background:#fff;padding:0;border-radius:5px;box-shadow:0 0.5px 5px 0 #e1e1e1;transition:box-shadow .1s ease,transform .1s ease}.repo__card:first-child,.repo__card--alt:first-child{margin-top:0}.repo__card:hover,.repo__card--alt:hover{cursor:pointer;background-color:#FAFAFA}.repo__card--alt{background-color:#523ee8;color:white}.repo__owner,.repo__owner--alt,.repo__title,.repo__title--alt,.repo__date{color:#523EE8;font-size:0.8rem;font-family:'Poppins', sans-serif;margin:0}.repo__owner-box{width:100%;display:flex;flex-direction:column;position:relative;align-items:center;padding:1rem}.repo__owner--alt{color:white}.repo__title,.repo__title--alt{font-weight:bold;font-size:1rem;margin-right:1rem}.repo__title--alt{color:white}.repo__top{width:100%;display:flex;justify-content:space-between;margin-bottom:1rem}.repo__date{position:absolute;bottom:1rem;left:1rem}.repo__content,.repo__content--alt{background-color:#F4F7FA;width:100%;padding:0.5rem}.repo__content--alt{border-top:0.5px solid white}.repo__folder{max-height:64px}

.log{padding:0 5rem;background-color:#F4F7FA;width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.log__logo{position:absolute;top:2rem;left:4rem;font-family:'Noto Serif', serif}.log__title{font-size:3rem;font-family:'Poppins', sans-serif}.log__blurb{width:30%}.log__img{width:700px;margin-left:2rem}.log__link{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.log__in{background-color:#006DFE;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:1rem;border-radius:10px;color:white;font-family:'Poppins', sans-serif;font-weight:400}.log__in:hover{background-color:#1A1A7C}

*{margin:0;font-family:Arial, Helvetica, sans-serif}.collab{background-color:#FAFAFA}.collab__cont{display:flex;width:100%}.collab__square{display:flex;width:632px;min-width:632px;flex-wrap:wrap}.collab__box{width:300px;height:190px;display:flex;position:relative;flex-direction:column;justify-content:center;align-items:center;background-color:white;border-radius:2px;box-shadow:0 0.5px 5px 0 #e1e1e1}.collab__box:nth-child(1){margin-right:2rem}.collab__box:nth-child(3){margin-right:2rem;margin-top:2rem}.collab__box:nth-child(4){margin-top:2rem}.collab__active{width:100%;height:408px;margin-left:2rem;background-color:white;border-radius:2px;box-shadow:0 0.5px 5px 0 #e1e1e1;background-image:url(/static/media/05.5fe64e7b.jpg);background-position:center;background-size:cover;display:flex;flex-direction:column;align-items:center;justify-content:center}.collab__active:after{background-color:rgba(255,255,255,0.4)}.collab__text,.collab__button{color:white;font-size:3rem;font-family:Arial, Helvetica, sans-serif}.collab__heading,.collab__heading--small{color:#0D294E;font-size:3rem;font-weight:bold;font-family:Arial, Helvetica, sans-serif;margin-bottom:0}.collab__heading--small{font-size:1.1rem;font-weight:normal}.collab__infograph{background-color:#0AC286;border-radius:8px;padding:0.5rem 1rem;color:white;position:absolute;top:1rem;left:1rem}.collab__button{padding:.5rem 3rem;border-radius:5px;display:flex;justify-content:center;background-color:white;color:#0D294E;font-size:1rem}.collab__button:hover{cursor:pointer}

.rep{padding:5rem 7rem;background-color:#FAFAFA;min-height:100vh}

.title{width:100%;padding:1rem;border-bottom:1px solid gray;margin-bottom:2rem}.title__name{color:#0D294E;font-size:2rem;font-family:Arial, Helvetica, sans-serif}

.any{width:100vw;min-height:100vh;padding:5rem 5rem;background-color:#FAFAFA;display:flex;flex-direction:column}.any__title{color:#0D294E;font-size:3rem;font-family:Arial, Helvetica, sans-serif}

*{margin:0}.user{border-radius:5px;box-shadow:0 0.5px 5px 0 #e1e1e1;height:100px;width:70%;display:flex;margin-bottom:1rem}.user__box{display:flex;flex-direction:column;padding:1rem}.user__short{margin-right:1rem;display:flex;flex-direction:column;align-items:center}.user__pic{height:100%;border-radius:5px}.user__data{display:flex}

